/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable jsx-a11y/anchor-has-content */
import React from "react";
import PropTypes from "prop-types";
import style from "./Filter.module.css";

export default function Filter({ value, onChangeFilter }) {
  return (
    <div>
      <label className={style.Serch}>FIND CONTACTS BY NAME</label>
      <input
        className={style.SerchInput}
        type="text"
        placeholder="Enter contact name"
        value={value}
        onChange={(e) => onChangeFilter(e.target.value)}
      />
      <a
        placeholder="download contact list"
        className={style.Download}
        download="contact list.xls"
        href="#"
        onclick="return ExcellentExport.excel(this, 'datatable', 'Sheet Name Here');"
        name="download contact list"
      >
        Download contact list
      </a>
      <table id="datatable">
        <tr>
          <td>100</td> <td>200</td> <td>300</td>
        </tr>
        <tr>
          <td>400</td> <td>500</td> <td>600</td>
        </tr>
      </table>
    </div>
  );
}

Filter.propTypes = {
  value: PropTypes.string.isRequired,
  onchangeFilter: PropTypes.func,
};
